<template>
  <div id="products_list">
    <div class="list_po">
      <div class="products_list_t">
        <van-search
          v-model="value"
          placeholder="请输入搜索关键词"
          @keydown.enter="book(value)"
        />
        <!-- icon-shitu-kapian -->
        <div
          class="products_list_ico iconfont"
          @click="changePsd"
          :class="[view_mode == 0 ? 'icon-liebiao' : 'icon-shitujuzhen']"
        ></div>
      </div>
      <!-- 顶部文字 -->
      <div class="products_list_t_text">
        <router-link tag="div" to="/sort" class="products_list_t_text_item">{{
          text
        }}</router-link>
        <div
          class="products_list_t_text_item products_list_t_price products_list_t_px"
          @click="changeIndex"
        >
          <span>价格</span>
          <div class="pro_img" >
            <!-- 未排序 -->
            <van-image
              v-if="price_ids == 0"
              src="data:image/png;base64,"
            />
            <!-- 升序 -->
            <van-image
              v-if="price_ids == 1"
              src="data:image/png;base64,"
            />
            <!-- 降序 -->
            <van-image
              v-if="price_ids == 2"
              src="data:image/png;base64,"
            />
          </div>
        </div>
        <div
          class="products_list_t_text_item products_list_t_xl products_list_t_px"
          @click="changeIndex2"
        >
          <span>销量</span>
          <div class="pro_img" >
            <!-- 未排序 -->
            <van-image
              v-if="xl_ids == 0"
              src="data:image/png;base64,"
            />
            <!-- 升序 -->
            <van-image
              v-if="xl_ids == 1"
              src="data:image/png;base64,"
            />
            <!-- 降序 -->
            <van-image
              v-if="xl_ids == 2"
              src="data:image/png;base64,"
            />
          </div>
        </div>
        <!-- :to="{name:'GoodsList',query:{id:id,title:text}}" -->
        <div class="products_list_t_text_item" @click="getNew">新品</div>
      </div>
    </div>
    <!-- 商品列表 -->
    <div class="products_list_goods" v-if="list.length">
      <ul
        :class="[
          view_mode == 0 ? 'products_list_goods_ul' : 'products_list_goods_ul2',
        ]"
      >
        <router-link
          tag="li"
          v-for="i in list"
          :key="i.id"
          :to="`/detail/${i.id}`"
          :class="[
            view_mode == 0
              ? 'products_list_goods_li'
              : 'products_list_goods_li2',
          ]"
        >
          <!-- 传入视图模式渲染 0为图标 1为列表 -->
          <component
            :is="type"
            :item="i"
            h="4.5rem"
            :mode="view_mode"
          ></component>
        </router-link>
      </ul>
    </div>
    <!-- 搜索没有结果时，推荐商品 -->
    <div v-if="!search_flag" class="productsSearch_ss_tj">
      <!-- 不存在搜索结果 -->
      <div class="productsSearch_none">
        <img src="http://47.115.51.185/h5/img/noCart.6f2abfef.png" alt="" />
      </div>
      <SearchNone :arr="referralArr"></SearchNone>
    </div>
  </div>
</template>
<script>
// 商品
import Psd from "../components/products/productsPsd.vue";
// 列表视图
import listPsd from "../components/products/productsList.vue";
// 搜索
import ProductsSearch from "../components/products/productsSearch.vue";
// 推荐
import SearchNone from "../components/search/SearchNone.vue";
export default {
  data() {
    return {
      list: [],
      b_list: [],
      //   搜索框值
      value: "",
      text: null,
      ids: false,
      //0表示无排序 1表示升序 2表示降序
      price_ids: 0,
      price_index: 3,
      // 销量排序
      xl_ids: 0,
      xl_index: 3,
      id: null,
      type: "Psd",
      // 视图改变
      view_i: 0,
      // view_mode 为0表示图标视图 为1表示列表视图
      view_mode: 0,
      // 搜索结果列表
      seoArr: [],
      // 有无搜索结果，true表示有
      search_flag: true,
      // 推荐搜索
      referralArr: [],
    };
  },
  components: {
    Psd,
    listPsd,
    ProductsSearch,
    SearchNone,
  },
  created() {
    // ${id}
    this.id = this.$route.query.id;
    let id = this.$route.query.id;
    this.text = this.$route.query.title;
    // console.log(this.$route.query.key);

    // 如果是分类搜索过来的
    let key = this.$route.query.key;
    if(key){
      this.text="默认";
      this.value=key;
      this.book(key);
      return ;
    }


    // 快速分类
    this.axios
      .get(
        `/api/products?page=1&limit=8&keyword=&sid=${id}&news=0&priceOrder=&salesOrder=`
      )
      .then((res) => {
        // console.log("选择",res);
        this.list = res.data.data;
        // 备用
        this.b_list = res.data.data.concat();
        if (this.list.length) {
          this.search_flag = true;
        } else {
          // 没有搜索结果
          this.search_flag = false;
          // 推荐商品
          this.axios.get("/api/product/hot?page=1&limit=20").then((res) => {
            this.referralArr = res.data.data;
          });
        }
      });
    // 新品
  },
  methods: {
    //   价格排序
    changeIndex() {
      this.price_index++;
      this.price_ids = this.price_index % 3;
      // 销量排序清空
      this.xl_index = 3;
      this.xl_ids = 0;
      if (this.price_ids == 0) {
        //   不排序
        this.list = this.b_list.concat();
      } else if (this.price_ids == 1) {
        //   升序
        this.list.sort(this.rank("price", true));
      } else if (this.price_ids == 2) {
        //   降序
        this.list.sort(this.rank("price", false));
      }
    },
    // 销量排序
    changeIndex2() {
      this.xl_index++;
      this.xl_ids = this.xl_index % 3;
      // 价格排序清空
      this.price_index = 3;
      this.price_ids = 0;
      if (this.xl_ids == 0) {
        //   不排序
        this.list = this.b_list.concat();
      } else if (this.xl_ids == 1) {
        //   升序
        this.list.sort(this.rank("sales", true));
      } else if (this.xl_ids == 2) {
        //   降序
        this.list.sort(this.rank("sales", false));
      }
    },
    // 排序函数
    rank(pro, flag) {
      return (a, b) => {
        a = a[pro];
        b = b[pro];
        // 升序
        if (flag) {
          return a - b;
        } else {
          return b - a;
        }
      };
    },
    // 新品商品请求
    getNew() {
      this.axios
        .get(
          "/api/products?page=1&limit=8&keyword=&sid="+this.id+"&news=1&priceOrder=&salesOrder="
        )
        .then((res) => {
          // console.log("新品",res);
          this.list = res.data.data;
          this.b_list = res.data.data.concat();
        });
    },
    // 改变视图方式
    changePsd() {
      this.view_i++;
      this.view_mode = this.view_i % 2;
      if (this.view_mode) {
        this.type = "listPsd";
      } else {
        this.type = "Psd";
      }
    },
    // 搜索框，按下enter搜索
    book(val) {
      // console.log(val);
      this.axios
        .get("/api/products?page=1&limit=20&keyword=" + val)
        .then((res) => {
          // console.log(res);
          let data = res.data.data;
          // this.seoArr=data;
          this.list = data;
          // console.log("搜索结果=>", this.list.length);
          this.b_list = data.concat();
          this.type = "ProductsSearch";
          if (!this.list.length) {
            // 没有搜索结果
            this.search_flag = false;
            // 推荐商品
            this.axios.get("/api/product/hot?page=1&limit=20").then((res) => {
              this.referralArr = res.data.data;
            });
          } else {
            this.search_flag = true;
          }
        });
    },
  },
};
</script>
<style lang="less" scoped>
#products_list {
  // 顶部
  width: 100vw;
  height: 100vh;
  background-color: #f5f5f5;
  .list_po {
    //   搜索
    .products_list_t {
      width: 100vw;
      box-sizing: border-box;
      background-color: #e93323 !important;
      display: flex;
      align-items: center;
      .van-search {
        background-color: transparent;
        flex: 1;
        .van-search__content {
          border-radius: 0.5rem;
        }
      }
      // 视图
      .products_list_ico {
        font-size: 0.9rem;
        padding-right: 0.3rem;
        color: #fff;
      }
    }
    // 文字
    .products_list_t_text {
      height: 0.86rem;
      background-color: #fff;
      color: #454545;
      font-size: 0.28rem;
      display: flex;
      justify-content: space-around;
      align-items: center;
      div.products_list_t_text_item:nth-child(1) {
        color: #fc4141 !important;
      }
      .products_list_t_text_item {
        flex: 1;
      }
      // 排序
      .products_list_t_px {
        display: flex;
        align-items: center;
        overflow: hidden;
        .pro_img {
          width: 0.25rem;
          margin-left: 0.1rem;
          overflow: hidden;
        }
      }
      // 新品
      div:nth-child(4) {
        color: #fc4141 !important;
      }
    }
  }
  // 商品
  .products_list_goods {
    width: 100vw;
    box-sizing: border-box;
    // padding: 0 0.3rem;
    // 图标视图
    .products_list_goods_ul {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      box-sizing: border-box;
      padding: 0 0.3rem;
      .products_list_goods_li {
        width: 4.6rem;
        background-color: #fff;
        margin-top: 0.3rem;
        border-radius: 0.1rem 0.1rem 0 0;
        overflow: hidden;
      }
    }
    // 列表视图
    .products_list_goods_ul2 {
      display: flex;
      align-items: center;
      position: relative;
      flex-wrap: wrap;
      background: #fff;
      .products_list_goods_li2 {
        width: 100%;
        box-sizing: border-box;
        padding: 0 0.3rem;
      }
    }
  }
  .productsSearch_none {
    width: 4.14rem;
    height: 3.36rem;
    margin: 0 auto;
    img {
      width: 100%;
      height: auto;
    }
  }
  .productsSearch_ss_tj {
    width: 100vw;
    box-sizing: border-box;
    padding: 0 0.3rem;
    background: #fff;
  }
}
</style>