<template>
  <div class="productsSearch">
    <!-- 存在搜索结果且为图标视图 -->
    <Psd :item="item" h="4.5rem" v-if="item&&mode==0"></Psd>
    <!-- 存在结果且为列表视图 -->
    <listPsd :item="item" h="4.5rem" v-if="item&&mode==1"></listPsd>
  </div>
</template>
<script>
// 图标视图
import Psd from "./productsPsd.vue";
// 列表视图
import listPsd from "./productsList.vue";
export default {
  // 存在item说明有搜索结果
  props: ["item","mode"],
  components: {
    Psd,
    listPsd
  },
  data() {
    return {
      referralArr: [],
      //   true表示有搜索结果，false表示没有搜索结果
      flag: false,
    };
  },
  created() {
    // console.log("item=>", this.item);
    if(this.item.id){
        this.flag=true;
    }
  },
};
</script>
